.UserIconWrp, .RightSection{
  display: inline-block;
}
.UserIconWrp{
  margin-right: 30px;
  background-color: #fff;
  border-radius: 50%;
  padding: 15px 10px 0;
  overflow: hidden;
}
.RightSection{
  width: 180px;
  vertical-align: top;
}
.Mail{
  font-size: 16px;
  color: #41424e;
  font-weight: 700;
  margin-bottom: 14px;
}
.ProgressWrp .UpperSection{
  overflow: hidden;
}
.ProgressWrp  .Profile{
  float: left;
  font-size: 14px;
  color: #41424e;
}
.ProgressWrp  .Percents{
  float: right;
  font-size: 14px;
  color: #83878c;
}
.ProgressWrp .BackLine{
  margin-top: 8px;
  width: 100%;
  height: 2px;
  background-color: #f0f0f0;
}
.ProgressWrp .BackLine .Progress{
  height: 4px;
  background-color: #922c88;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  border-radius: 3px;
}



